// var ac = new AudioContext();
var ac = new OfflineAudioContext(1,44100*40, 44100);
var audioBuffer = null;
var playSource = null;

function bufferToBase64 (buffer) {
    var bytes = new Uint8Array(buffer);
    var len = buffer.byteLength;
    var binary = "";
    for (var i = 0; i < len; i++) {
        binary += String.fromCharCode(bytes[i]);
    }
    return window.btoa(binary);
};

function toMono(data) {

    var source = ac.createBufferSource();
    source.buffer = data;

    var splitter = ac.createChannelSplitter(2);
    source.connect(splitter);

    var merger = ac.createChannelMerger(1);
    splitter.connect(merger, 1, 0);
    splitter.connect(merger, 0, 0);

    merger.connect(ac.destination);

    ac.startRendering().then(function(renderedBuffer) {
        /* alert('Rendering completed successfully'); */
        /* alert("renderedBuffer sample rate:" + renderedBuffer.sampleRate); */
        /* alert("renderedBuffer Duration:" + renderedBuffer.duration); */
        /* alert("renderedBuffer length:" + renderedBuffer.length); */
        /* alert("renderedBuffer Number of channels:" + renderedBuffer.numberOfChannels); */
        audioBuffer = renderedBuffer;

        }).catch(function(err) {
          console.log('Rendering failed: ' + err);
        });
}

function playSound() {
    if (audioBuffer) {
        var ac = new AudioContext();
        // source is global so we can call .stop() later.
        playSource = ac.createBufferSource();
        playSource.buffer = audioBuffer;
        playSource.loop = false;
        playSource.connect(ac.destination);
        playSource.start(0); // Play immediately.
    }
}

function stopSound() {
    if (playSource) {
        playSource.stop(0);
    }
}
function loadSound(buffer) {
    ac.decodeAudioData(buffer, function (buffer) {
        // success callback, buffer is a https://developer.mozilla.org/en-US/docs/Web/API/AudioBuffer
        audioBuffer = buffer;
        /* alert("src sample rate:" + buffer.sampleRate); */
        /* alert("src Duration:" + buffer.duration); */
        /* alert("src length:" + buffer.length); */
        /* alert("src Number of channels:" + buffer.numberOfChannels); */

        var mono = toMono(buffer)
        /* alert("mono sample rate:" + mono.sampleRate); */
        /* alert("mono Duration:" + mono.duration); */
        /* alert("mono length:" + mono.length); */
        /* alert("mono Number of channels:" + mono.numberOfChannels); */

    }, function (e) {
        console.log('Error decoding file', e);
    });}

class NameForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: ''};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.files[0]});
  }

  handleSubmit(event) {
   
    var reader = new FileReader();
    reader.onload = function (e) {
       loadSound(this.result)
    };

    reader.readAsArrayBuffer(this.state.value);

    event.preventDefault();
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
        Load file:
        <input type="file" accept="audio" onChange={this.handleChange} />
        </label>
        <input type="submit" value="Load"  variant="primary" />
      </form>
    );
  }
}

class PlayPauseButton extends React.Component {
  handlePlay() {
      playSound();
  }
  handleStop() {
      stopSound();
  }
  handlePCM() {
      var pcmFloat = audioBuffer.getChannelData(0);
      var pcmInt16 = new Int16Array(pcmFloat.length);
      for (var i = 0; i < pcmFloat.length; i++) {
          let s = Math.max(-1, Math.min(1, pcmFloat[i]));
          pcmInt16[i] = s < 0 ? s * 0x8000 : s * 0x7FFF;
      }      
      alert(pcmInt16);
  }

  render() {
      // This syntax ensures `this` is bound within handleClick
      return (<div>
              <Button variant="primary" onClick={() => this.handlePlay()}>Play</Button>
              <Button variant="primary" onClick={() => this.handleStop()}>Stop</Button>
              <Button variant="primary" onClick={() => this.handlePCM()}>PCM</Button>
              </div>
          );
  }
}

let device;

class WebUsbTest extends React.Component {
  handleTest() {
      navigator.usb.requestDevice({ filters: [] })
          .then(device => {
                  alert(device.productName);      // "Arduino Micro"
                  alert(device.manufacturerName); // "Arduino LLC"
              })
          .catch(error => { console.error(error); });

      
      navigator.usb.getDevices()
          .then(devices => {
                  alert("Total devices: " + devices.length);
                  devices.forEach(device => {
                          alert("Product name: " + device.productName + ", serial number " + device.serialNumber);
                      });
              });
  }

  render() {
      return (<div>
              <Button variant="primary" onClick={() => this.handleTest()}>Test</Button>
              </div>
          );
  }
}

function App() {
  return (
    <div className="App">
      <NameForm />
      <PlayPauseButton />
      <WebUsbTest />
    </div>
  );
}

export default App;
